*{
	scroll-behavior: smooth;
}
body{
	margin: 0px;
	padding: 0px;
	background-color: rgb(255, 255, 255);
}
.bodybefore{
	overflow: hidden;
}
#spice1{
	height: 90vh;
	overflow-x: hidden;
	position: relative;
	padding: 15px;
	font-family: 'Six Caps', sans-serif;
	font-family: 'Michroma', sans-serif;
    font-size: 20vw;
    display: flex;
    margin-top: 38vh;
    margin-top: 8vmax;
    justify-content: center;
	color: black;
	font-weight: bold;
	-webkit-animation: spice1 3s ease-in-out infinite;
	        animation: spice1 3s ease-in-out infinite;
	text-shadow: 
	/*3D layers*/
	0px 0px 1px rgb(255, 0, 0),
	0px 1px 1px rgb(255, 0, 0),
	0px 2px 1px rgb(255, 0, 0),
	1px 1px 1px rgb(255, 0, 0),
	1px 2px 1px rgb(255, 0, 0),
	1px 3px 1px rgb(255, 0, 0),
	2px 2px 1px rgb(255, 0, 0),
	2px 3px 1px rgb(255, 0, 0),
	2px 4px 1px rgb(255, 0, 0),
	2px 3px 1px rgb(122, 1, 1),
	2px 4px 1px rgb(122, 1, 1),
	2px 3px 1px rgb(122, 1, 1),
	2px 4px 1px rgb(255, 0, 0),
	10px 10px 11px rgb(122, 1, 1),
	10px 10px 11px rgb(122, 1, 1),
	10px 10px 11px rgb(122, 1, 1),
	/*long soft bottom*/
	1px 10px 30px rgba(0,0,0,0.8),
	1px 10px 70px rgba(0,0,0,0.8),
	/*short soft bottom*/
	0px 5px 5px rgba(0,0,0,0.8),
	/*left shadow*/
	-5px 5px 20px rgba(0,0,0,0.8),
	/*right shadow*/
	5px 5px 20px rgba(0,0,0,0.8);
}
#spice1::after{
	overflow-x: hidden;
	content: "SPICE";
	position: absolute;
	font-family: 'Six Caps', sans-serif;
	font-family: 'Michroma', sans-serif;
    font-size: 20vw;
    display: flex;
    justify-content: center;
	font-weight: bold;
	-webkit-animation: spice1 3s ease-in-out infinite;
	        animation: spice1 3s ease-in-out infinite;
	text-shadow: 0px 0px 100px rgba(11,124,199,0.8);
	-webkit-animation: cycle 10s linear infinite;
	        animation: cycle 10s linear infinite;
}
.scrolldown{
	position: absolute;
	bottom: -65px;
	left: 50%;
	width: 15px;
	height: 15px;
	margin-left: -15px;
	/* background: red; */
	transform: translateY(-80px) translateX(50%) rotate(45deg);
}
.scrolldown span{
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	opacity: 0;
}
.scrolldown span{
    border-bottom: 2.5px solid black;
    border-right: 2.5px solid black;
    -webkit-animation: scroll 1s  ease-in-out infinite;
            animation: scroll 1s  ease-in-out infinite;
}
.scrolldown span:nth-child(3){
	transform: translate(15px,15px);
		-webkit-animation-delay: 0s;
		        animation-delay: 0s;
}
@media (min-width: 0px) and (max-width: 575px){
