}
.social{
	display: flex;
	position: fixed;
	bottom: 8%;
    right: 50%;
    display: flex;
    justify-content: center;
	/* left: 48%; */
	transform: translate(50%,-50%);
	padding: 0;
}
.social li{
    display: flex;
    justify-content: center;
	list-style: none;
}
.social li a{
	width: 35px;
	height: 35px;
	text-align: center;
	line-height: 30px;
	font-size: 16px;
	margin: 0 3px;
	display: block;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
	border: 1px black;
	z-index: 1;
}
.social li a .fa{
	position: relative;
	color: 	#C8C8C8;
	transition: .5s;
	z-index: 3;
}
.social li a:hover .fa{
	color: #fff;
	transform: rotateY(360deg);
	font-size: 25px;
}
.social li:nth-child(2) a:hover .fa{
  color: #F9F6EF;
}
.social li:nth-child(1) a:hover .fa{
  color: #1DB954;
}
.social li:nth-child(5) a:hover .fa{
  color: #4267B2;
}
.social li:nth-child(3) a:hover .fa{
  color: #FF0000;
}
.social li:nth-child(4) a:hover .fa{
  color: #C13584;
}
.social li a:before{
	content: "";
	position: relative;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	/* background: #f00; */
	transition: .5s;
	z-index: 2;
}
